  }),
  metaInfo: {
    title: 'Đứa con trời đánh hay truyện Tiếc gà chôn mẹ - Truyện cổ Việt Nam',
    meta: [
    { charset: 'utf-8' },
      { name: 'Keywords', content: 'Đứa con trời đánh hay là truyện Tiếc gà chôn mẹ' },
      { name: 'Description', content: 'Truyện kể về một người đàn ông xấu tính lại có thói ham chơi chọi gà. Vì chẳng may người vợ làm chết con gà mà hắn đã đem chôn sống người mẹ của mình. May thay Ngọc Hoàng nhìn thấy và đã trừng phạt hắn một cách thích đáng.' },
